import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AuthService } from '../auth/auth.service';
import { Config } from '../../config';
import { Observable } from 'rxjs/Observable';
import { Insider } from './model/insider';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class InsiderService {

  
  private url: string;
  constructor(private httpClient: HttpClient,
    private authService: AuthService) {
    let config = new Config()
    this.url = config.getRest()
  }

  public getInsidersById(id: Number): Observable<Insider[]> {
    let subject = new Subject<Insider[]>()
    this.httpClient.get(`${this.url}/insider/${id}`, { headers: this.authService.getHeader() }).subscribe(response => {
      subject.next(response as Insider[])
    }, error => {
      subject.error(error)
    });
    return subject.asObservable();
  }

}



